import {Component, OnInit} from '@angular/core';

export class TableRow {
  zoom: number;
  FovMulti: number;
  ADSMulti: number;
  newADS: number;
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  title = 'hard-calc';
  name = '';
  sensitivity = 1;
  aspectRatio = 16 / 9;
  Ratios = [{
    name: '16:9',
    value: 16 / 9,
    id: '1'
  }, {
    name: '16:10',
    value: 16 / 10,
    id: '2'
  }, {
    name: '4:3',
    value: 4 / 3,
    id: '3'
  }, {
    name: '3:2',
    value: 3 / 2,
    id: '4'
  }, {
    name: '5:3',
    value: 5 / 3,
    id: '5'
  }, {
    name: '5:4',
    value: 5 / 4,
    id: '6'
  }, {
    name: '19:10',
    value: 19 / 10,
    id: '7'
  }, {
    name: '21:9',
    value: 21 / 9,
    id: '8'
  }];
  angle = 60;
  result = 0;
  rowsData: TableRow[] = [
    {ADSMulti: 0.6, FovMulti: 0.9, zoom: 1, newADS: 0},
    {ADSMulti: 0.59, FovMulti: 0.59, zoom: 1.5, newADS: 0},
    {ADSMulti: 0.49, FovMulti: 0.49, zoom: 2, newADS: 0},
    {ADSMulti: 0.42, FovMulti: 0.42, zoom: 2.5, newADS: 0},
    {ADSMulti: 0.35, FovMulti: 0.35, zoom: 3, newADS: 0},
    {ADSMulti: 0.3, FovMulti: 0.3, zoom: 4, newADS: 0},
    {ADSMulti: 0.22, FovMulti: 0.22, zoom: 5, newADS: 0},
    {ADSMulti: 0.14, FovMulti: 0.092, zoom: 12, newADS: 0}];
  dataSource = this.rowsData;
  displayedColumns = ['zoom', 'newADS'];


  ngOnInit(): void {
  }

  calculate() {

  }

  formatLabel(value: number) {
    if (value >= 1000) {
      return Math.round(value / 1000) + 'k';
    }

    return value;
  }

  onUnfocus() {
    if (this.angle < 30) {
      this.angle = 30;
    }
    if (this.angle > 90) {
      this.angle = 90;
    }
    if (this.sensitivity < 1) {
      this.sensitivity = 1;
    }
    if (this.sensitivity > 200) {
      this.sensitivity = 200;
    }
    this.recalc();
  }

  recalc() {
    this.rowsData.forEach(rd => {
      let newVerticalFov = this.angle;
      let tan1 = Math.tan((this.angle / 2) * Math.PI / 180);
      let tan2 = Math.tan((tan1 * this.aspectRatio) * Math.PI / 180);
      let horizontalFov = 2 / tan2;
      if (horizontalFov > 150) {
        newVerticalFov = 2 / Math.tan(Math.tan(75 * Math.PI / 180) / this.aspectRatio);
      }
      let tan3 = Math.tan((rd.FovMulti * newVerticalFov / 2) * Math.PI / 180);
      let tan4 = Math.tan((newVerticalFov / 2) * Math.PI / 180);
      let FovAdj = tan3 / tan4;
      rd.newADS = Math.round((rd.ADSMulti / FovAdj) * this.sensitivity);
    });
  }
}
